<template>
	<el-dialog title="" :visible.sync="dialogVisible" width="30%">
		<el-form :model="psw">
			<el-form-item label="">
				<el-input type="password" v-model="psw.oriPsw" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input type="password" v-model="psw.newPsw" auto-complete="off"></el-input>
			</el-form-item>
			<!-- <el-form-item label="">
				<el-input type="password" v-model="psw.newPsw" auto-complete="off"></el-input>
			</el-form-item> -->
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="dialogVisible = false"></el-button>
			<el-button size="small" type="primary" @click="updatePassword"></el-button>
		</div>
	</el-dialog>
</template>

<script>
	import {updatePsw} from '@/network/user.js'
	export default {
		name: 'updatePassword',
		data() {
			return {
				psw: {
					newPsw: '',
					oriPsw: ''
				},
				dialogVisible : true
			}
		},
		props: ['show'],
		watch: {
			show: function(newVal) {
				this.dialogVisible = newVal.valueOf();
			}
		},
		methods: {
			updatePassword() {
				updatePsw(this.psw).then(res => {
					if(res.code === 1) {
						this.dialogVisible = false;
						Object.keys(this.psw).forEach(key => (this.psw[key] = ''))
						this.$message({
							showClose: true,
							message: res.msg,
							duration: 1000,
							type: 'success'
						});
					}else {
						this.$message({
							showClose: true,
							message: res.msg,
							duration: 1000,
							type: 'error'
						});
					}
				})
			}
		},
		updated() {
			console.log(this.show)
		}
	}
</script>

