<template>
	<el-dialog
		title=""
		:visible.sync="dialogVisible"
		width="25%"
		:before-close="handleClose"
	>
		<div class="headerImg-container">
			<div class="hc__imgSelect">
				<div class="imgSelect_title">
					<h3></h3>
					<span v-show="imgSelect" @click="updateImg"></span>
				</div>
				<div class="imgSelect_show">
					<el-upload
					v-show="!imgSelect"
					ref="upload"
					drag
					:show-file-list="false"
					:limit="1"
					action=""
					:on-change="loadingImg"
					:on-exceed="handleExceed"
					:auto-upload="false"
					>
					<div class="el-upload__tip">
						<p>5MBJPGPNG</p>
						<p>()</p>
					</div>
					</el-upload>
					<img id="cropImg" v-show="imgSelect"/>
				</div>
			</div>
			<div class="hc__imgPreview">
				<h3></h3>
				<div class="previewBoxMax"></div>
				<div class="previewBoxMiddle"></div>
				<div class="previewBoxSmall"></div>
			</div>
		</div>
		<div class="imgSelect__footer" slot="footer">
			<el-button size="small" @click="dialogVisible = false"></el-button>
			<el-button size="small" type="primary" @click="imgSave"></el-button>
		</div>
	</el-dialog>
</template>

<script>
	import Cropper from 'cropperjs'
	import 'cropperjs/dist/cropper.css'
	import {updateUserHeadImg} from '@/network/user.js'
	export default {
		name: 'uploadPhoto',
		data () {
			return {
				imgSelect: false,
				myCropper: null,
				afterimg: null,
				dialogVisible : true
			}
		},
		props: ['show'],
		watch: {
			show: function(newVal) {
				this.dialogVisible = newVal.valueOf();
			}
		},
		methods: {
			//up-load
			updateImg() {
				this.imgSelect = false;
				this.myCropper.destroy();
			},
			//
			handleExceed() {
				this.$message('1');
			},
			loadingImg(file){
				this.$refs.upload.clearFiles();
				const isFileType = ['image/jpeg', 'image/png'];
				const sucessFileType = isFileType.find(item => item === file.raw.type);
				const sucessFile5m = file.size / 1024 / 1024 / 5;
				//
				let reader = new FileReader();
				if(sucessFileType && sucessFile5m) {
					this.imgSelect = true
					//readAsDataURLFiledata:URLbase64
					reader.readAsDataURL(file.raw);
					reader.onload = () =>{
						let dataURL = reader.result;
							//imgsrc
							document.querySelector('#cropImg').src = dataURL;
							const image = document.getElementById('cropImg');
							//cropper-----------------------------------------
							this.myCropper = new Cropper(image, {
								viewMode: 1,
								dragMode: 'move',
								initialAspectRatio: 1,
								aspectRatio: 1,
								background: false,
								autoCropArea: 0.6,
								zoomOnWheel: true,
								preview: [ document.querySelector('.previewBoxMax'),
													document.querySelector('.previewBoxMiddle'),
													document.querySelector('.previewBoxSmall')]
							})
					}
					//
				}else {
					this.$message({
						message:'5MBJPGPNG',
						type: 'error'
					})
				}
			},
			imgSave() {
				//base64 -> blob -> file
				if( ! this.imgSelect) {
					this.$message('')
				}else {
					let newImg = this.myCropper.getCroppedCanvas({	//base64
						imageSmoothingQuality: 'high'
					}).toDataURL('image/jpeg') 
					var blob = this.getBlob(newImg);		//blob
					// console.log(blob)
					const file = new window.File(			//file
						[blob],
						'jpg',
						{ type: blob.type }
					);							
					// console.log(file)
					let formdata = new FormData();
					formdata.append('file', file);
					updateUserHeadImg(formdata).then(res => {
						this.afterimg = this.$store.state.getURL + res.data
						this.$store.dispatch('user/saveUserInfo')
						this.dialogVisible = false
					})
				}
			},
			//
			getBlob(base64Data) {
					let arr = base64Data.split(','),
							fileType = arr[0].match(/:(.*?);/)[1],
							bstr = atob(arr[1]),
							l = bstr.length,
							u8Arr = new Uint8Array(l);
							
					while (l--) {
							u8Arr[l] = bstr.charCodeAt(l);
					}
					return new Blob([u8Arr], {
							type: fileType
					});
			},
			handleClose(done) {
				// this.dialogVisible = false
				done()
			}
		}
	}
</script>

<style>
	.headerImg-container {
		display: flex;
		justify-content: space-between;
	}
	.imgSelect_title, .hc__imgPreview>h3{
		margin-bottom: 20px;
	}
	.imgSelect_title {
		display: flex;
		justify-content: space-between;
	}
	/*  */
	.imgSelect_show {
		width: 250px;
		height: 250px;
		overflow: hidden;
	}
	.el-upload {
		width: 100% !important;
		height: 100% !important;
	}
	.el-upload-dragger {
		width: 100% !important;
		height: 250px;
	}
	.el-upload__tip {
		display: flex;
		margin: 0 !important;
		height: 100%;
		padding: 0 10px;
		align-items: center;
		justify-content: center;
		text-align: center;
		flex-direction: column;
	}
	#cropImg {
		opacity: 0;
		display: none;
	}
	
	/*  */
	.hc__imgPreview>div {
		border: 1px solid #ebebeb;
		margin-bottom: 20px;
	}
	.previewBoxMax {
		height: 70px;
		width: 70px;
		overflow: hidden;
	}
	.previewBoxMiddle {
		height: 50px;
		width: 50px;
		overflow: hidden;
	}
	.previewBoxSmall {
		height: 20px;
		width: 20px;
		overflow: hidden;
	}
</style>
