<template>
	<div class="setting-container">
		<div class="section__container">
			<div class="section__header"></div>
			<div class="section__body">
				<el-row type="flex" class="section__row-item" justify="space-between" align="middle">
					<el-col :span="5"></el-col>
					<el-col :span="12"><img :src="userinfo.userImg" class="profile-photo"/></el-col>
					<el-col :span="5" class="col-item__button" @click.native="changeImg"></el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5"></el-col>
					<el-col :span="12">{{userinfo.userName}}</el-col>
					<el-col :span="5" class="col-item__button" @click.native='changeName'></el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5"></el-col>
					<el-col :span="12">{{userinfo.userEmail}}</el-col>
					<el-col :span="5" class="col-item__button" @click.native="changeEmail"></el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5"></el-col>
					<el-col :span="12">**********</el-col>
					<el-col :span="5" class="col-item__button" @click.native="changePassword"></el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5"></el-col>
					<el-col :span="12">{{userinfo.userTelephone}}</el-col>
					<el-col :span="5" class="col-item__button" @click.native="changePhone"></el-col>
				</el-row>
			</div>
		</div>
		<component :is="comp" :show="show"></component>
	</div>
</template>

<script>
	import {
		updateUserName,
		updateEmail,
		updateTelephone,
	} from "@/network/user.js";
	import uploadPhoto from '@/components/content/profile/uploadPhoto.vue'
	import updatePassword from '@/components/content/profile/updatePassword.vue'
	export default {
		name: 'Setting',
		data() {
			return {
				comp: '',
				show: ''
			}
		},
		components: {
			uploadPhoto,
			updatePassword
		},
		computed: {
			userinfo() {
				return this.$store.getters['user/getuserInfo']
			}
		},
		methods: {
			//
			changeImg() {
				this.comp = 'uploadPhoto';
				this.show = new Boolean(true)
			},
			//
			changeName() {
				this.$prompt("", "", {
					confirmButtonText: "",
					cancelButtonText: "",
				}).then(({ value }) => {
					let formData = new FormData();
					formData.append('userName', value)
					updateUserName(formData).then((res) => {
						console.log(res);
					})
				}).catch(() => {});
			},
			//
			changeEmail() {
				this.$prompt("", "", {
					confirmButtonText: "",
					cancelButtonText: "",
					inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
					inputErrorMessage: "",
				})
					.then(({ value }) => {
						updateEmail(value).then(res => {
							console.log(res)
							this.$message({
								type: "success",
								message: res.msg
							});
							this.userinfo.userEmail = value
						});
					}).catch(() => {})
			},
			//
			changePassword() {
				this.comp = 'updatePassword';
				this.show = new Boolean(true)
			},
			//
			changePhone() {
				this.$prompt("", "", {
					confirmButtonText: "",
					cancelButtonText: "",
				}).then(({ value }) => {
						updateTelephone(value).then(res => {
							console.log(res)
							this.$message({
								type: "success",
								message: res.msg,
							});
							this.userinfo.userTelephone = value;
						});
					}).catch(() => {})
			}
		}
		// created() {
		// 	getUserInfo().then(res => {
		// 		this.userinfo = res.data
		// 		this.userinfo.userImg = this.$store.getters['user/getUserInfo'].userImg;
		// 	})
		// }
	}
</script>

<style>
	.section__container {
		padding: 1rem;
	}
	.section__header {
		font-weight: 700;
		font-size: 1.5rem;
		margin-bottom: 1.5rem;
	}
	.section__row-item {
		padding: 1.5rem 0;
		border-top: 1px solid #dfdfdf;
	}
	.rsection__row-item:last-child {
		padding-bottom: 0;
	}
	.profile-photo {
		width: 80px;
		height: 80px;
		border-radius: 40px;
		border: 1px solid #ebebeb;
	}
	.col-item__button {
		text-align: right;
		color: var(--color-text-change);
	}
	.col-item__button:hover {
		color: var(--color-btn-focus);
	}
</style>
